@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@300;600;800&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,700;0,900;1,400&display=swap');

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body {
  font-family: 'Source Sans Pro', sans-serif;
}
ul{
  list-style: none;
  padding:0;
  margin:0;
}
a{
  text-decoration: none;
}
.container{
  padding-left: 2rem;
  padding-right: 2rem;
  height:100%;
  margin: 0 auto;
}
@media (min-width:768px){
  .container{
    width:750px;
  }
}
@media (min-width:769px) and (max-width: 991px){
  .container{
    width:950px;
  }
}
@media (min-width:991px) and (max-width: 1200px){
  .container{
    width:1170px;
  }
}